* {
  box-sizing: border-box;
  --select-link-color: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

body {
  box-sizing: border-box;
  background-color: #7f6e6e;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  top: 0;
  left: 0;
}

.logo {
  max-height: 40px;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__link:hover {
  color: var(--select-link-color);
}

.is-active {
  color: var(--select-link-color);
  height: 60px;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--select-link-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

